import React, { useState, useEffect } from 'react';
import axios from 'axios';
import gif from '../../loading-wheel.gif';
import pokeData from  "../../pokeDataClean.json"

const type_colors = {
  Bug: 'B1C12E',
  Dark: '4F3A2D',
  Dragon: '755EDF',
  Electr: 'FCBC17',
  Fairy: 'F4B1F4',
  Fight: '823551D',
  Fire: 'E73B0C',
  Flying: 'A3B3F7',
  Ghost: '6060B2',
  Grass: '74C236',
  Ground: 'D3B357',
  Ice: 'A3E7FD',
  Normal: 'C8C4BC',
  Poison: '934594',
  Psychc: 'ED4882',
  Rock: 'B9A156',
  Steel: 'B5B5C3',
  Water: '3295F6'
};

const Pokemon = (props) => {
  const { match } = props
  const { params } = match
  const { indexNum } = params
  
  const [pokemon, setPokemon] = useState(undefined)
  const [bio, setBio] = useState('')
  const [genderRatioMale, setGenderRatioMale] = useState(0)
  const [genderRatioFemale, setGenderRatioFemale] = useState(0)
  const [catchRate, setCatchRate] = useState(0)
  const [growthRate, setGrowthRate] = useState('')
  const [hatchSteps, setHatchSteps] = useState(0)

  useEffect(() => {
    axios.get(`https://pokeapi.co/api/v2/pokemon/${indexNum}/`)
      .then (res => {
        const { data } = res
        // console.log(data)
        setPokemon(data)
      })
      .catch(err => {
        setPokemon(false)
      })
  }, [indexNum])

  useEffect(() => {
    axios.get(`https://pokeapi.co/api/v2/pokemon-species/${indexNum}/`)
      .then(res => {
        const { data } = res
        let description = '';
        // console.log(data)
        data.flavor_text_entries
          .some(flavor => {
            const { flavor_text, language } = flavor
            const { name } = language
            if (name === 'en') {
              description = flavor_text
              // console.log(description)
              setBio(description)
            }
            const affirm1 = console.log('Description set as state for bio')
            return affirm1
          })

        const femaleRate = data.gender_rate
        setGenderRatioFemale(12.5 * femaleRate)
        setGenderRatioMale(12.5 * (8 - femaleRate))

        setCatchRate(pokeData[indexNum-1].catchrate)

        setGrowthRate(pokeData[indexNum-1].growthrate)

        setHatchSteps(255 * (pokeData[indexNum-1].stepshatch) + 1)
      })
  }, [indexNum])

  const loadPokemonJsx = () => {
    const { 
      name, 
      id, 
      abilities,  
      height, 
      weight,  
      types, 
      stats, 
      sprites
    } = pokemon

    const front_default  = `/pokedex/sprites/${pokeData[id-1].id}.png.gif`
    const fullImageUrl = `/pokedex/sprites/${pokeData[id-1].id}.png.gif`

    let [
      hp, 
      attack, 
      defense, 
      speed, 
      specialAttack, 
      specialDefense
    ] = ''

    stats.map(stat => {
      switch (stat.stat.name) {
        case 'hp':
          hp = pokeData[id-1].hp;
          // console.log(`HP: ${hp}`)
          break;
        case 'attack':
          attack = pokeData[id-1].attack;
          // console.log(`ATK: ${attack}`)
          break;
        case 'defense':
          defense = pokeData[id-1].def;
          // console.log(`DEF: ${defense}`)
          break;
        case 'speed':
          speed = pokeData[id-1].speed;
          // console.log(`SPEED: ${speed}`)
          break;
        case 'special-attack':
          specialAttack = pokeData[id-1].spatk;
          // console.log(`SP. ATK: ${specialAttack}`)
          break;
        case 'special-defense':
          specialDefense = pokeData[id-1].spdef;
          // console.log(`SP. DEF: ${specialDefense}`)
          break;
        default:
          break;
      }
      const affirm2 = console.log('Variables from switch case are set')
      return affirm2
    })

    return (
      <>
        <div 
          className='col'
          style={{
            marginTop: '25px'
          }}
        >
          <div className='card'>
            <div className='card-header'>
              <div className='row'>
                <div className='col-5'>
                  <h5>
                    <img 
                      src='https://icon-library.com/images/small-pokeball-icon/small-pokeball-icon-4.jpg' 
                      alt='Pokéball Icon'
                      width='45.714px'
                      height='36.285px'
                    />
                    {indexNum}
                  </h5>
                </div>
                <div className='col-7'>
                  <div 
                    style={{
                      display: 'flex',
                      flexDirection: 'row-reverse'
                    }}
                  >
                    <span 
                        key={pokeData[indexNum-1].type1}
                        className='badge badge-primary badge-pill mr-1'
                        style={{
                          backgroundColor: `#${type_colors[pokeData[indexNum-1].type1
                            .toLowerCase()
                            .split(' ')
                            .map(letter => letter.charAt(0).toUpperCase() + letter.substring(1))
                            .join(' ')]}`,
                          color: 'white'
                        }}
                      >
                        {`${pokeData[indexNum-1].type1
                            .toLowerCase()
                            .split(' ')
                            .map(letter => letter.charAt(0).toUpperCase() + letter.substring(1))
                            .join(' ')
                          }`
                        }
                      </span>
					  <span 
                        key={pokeData[indexNum-1].type2}
                        className='badge badge-primary badge-pill mr-1'
                        style={{
                          backgroundColor: `#${type_colors[pokeData[indexNum-1].type2]}`,
                          color: 'white'
                        }}
                      >
                        {`${pokeData[indexNum-1].type2
                            .toLowerCase()
                            .split(' ')
                            .map(letter => letter.charAt(0).toUpperCase() + letter.substring(1))
                            .join(' ')
                          }`
                        }
                      </span>
                  </div>
                </div>
              </div>
            </div>
            <div className='card-body'>
              <div className='row align-items-center'>
                <div className='col-md-9'>
                  <h5 className='mx-auto'>
                    { pokeData[indexNum-1].Name
                      .toLowerCase()
                      .split('-')
                      .map(letter => letter.charAt(0).toUpperCase() + letter.substring(1))
                      .join(' ')
                    }
                    <img
                      src={ front_default }
                      alt='Pokémon Sprite' 
                    />
                  </h5>
                  <div className='row align-items-center'>
                    <div className='col-12 col-md-3'>HP</div>
                    <div className='col-12 col-md-9'>
                      <div className='progress'>
                        <div 
                          className='progress-bar'
                          role='progressbar'
                          style={{
                            width: `${hp}%`
                          }}
                          aria-valuenow='25'
                          aria-valuemin='0'
                          aria-valuemax='200'
                        >
                          <small>{hp}</small>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className='row align-items-center'>
                    <div className='col-12 col-md-3'>Attack</div>
                    <div className='col-12 col-md-9'>
                      <div className='progress'>
                        <div 
                          className='progress-bar'
                          role='progressbar'
                          style={{
                            width: `${attack}%`
                          }}
                          aria-valuenow='25'
                          aria-valuemin='0'
                          aria-valuemax='200'
                        >
                          <small>{attack}</small>
                        </div>
                      </div>
                    </div>
                  </div>                  
                  <div className='row align-items-center'>
                    <div className='col-12 col-md-3'>Defense</div>
                    <div className='col-12 col-md-9'>
                      <div className='progress'>
                        <div 
                          className='progress-bar'
                          role='progressbar'
                          style={{
                            width: `${defense}%`
                          }}
                          aria-valuenow='25'
                          aria-valuemin='0'
                          aria-valuemax='200'
                        >
                          <small>{defense}</small>
                        </div>
                      </div>
                    </div>
                  </div>
				  <div className='row align-items-center'>
                    <div className='col-12 col-md-3'>Sp. Attack</div>
                    <div className='col-12 col-md-9'>
                      <div className='progress'>
                        <div 
                          className='progress-bar'
                          role='progressbar'
                          style={{
                            width: `${specialAttack}%`
                          }}
                          aria-valuenow='25'
                          aria-valuemin='0'
                          aria-valuemax='200'
                        >
                          <small>{specialAttack}</small>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className='row align-items-center'>
                    <div className='col-12 col-md-3'>Sp. Defense</div>
                    <div className='col-12 col-md-9'>
                      <div className='progress'>
                        <div 
                          className='progress-bar'
                          role='progressbar'
                          style={{
                            width: `${specialDefense}%`
                          }}
                          aria-valuenow='25'
                          aria-valuemin='0'
                          aria-valuemax='200'
                        >
                          <small>{specialDefense}</small>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className='row align-items-center'>
                    <div className='col-12 col-md-3'>Speed</div>
                    <div className='col-12 col-md-9'>
                      <div className='progress'>
                        <div 
                          className='progress-bar'
                          role='progressbar'
                          style={{
                            width: `${speed}%`
                          }}
                          aria-valuenow='25'
                          aria-valuemin='0'
                          aria-valuemax='200'
                        >
                          <small>{speed}</small>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className='row mt-2'>
                  <div className='col'>
                    <p className='p-2'></p>
                  </div>
                </div>
              </div>
            </div>
            <hr />
            <div className='card-body'>
              <h5 className='card-title text-center'>Profile</h5>
              <div className='row'>
                <div className='col-md-6'>
                  <div className='row flex-container'>
                    <div className='col-md-6'>
                      <h6 style={{display: 'flex'}}>Approx. Height:</h6>
                    </div>
                    <div className='col-md-6'>
                      <h6 style={{
                        display: 'flex',
                        flexDirection: 'row-reverse'
                      }}
                      >
                        Not implemented
                      </h6>
                    </div>
                  </div>
                  <div className='row flex-container'>
                    <div className='col-md-6'>
                      <h6 style={{display: 'flex'}}>Approx. Weight:</h6>
                    </div>
                    <div className='col-md-6'>
                      <h6 style={{
                        display: 'flex',
                        flexDirection: 'row-reverse'
                      }}
                      >
                        Not implemented
                      </h6>
                    </div>
                  </div>
                  <div className='row flex-container'>
                    <div className='col-md-6'>
                      <h6 style={{display: 'flex'}}>Catch Rate:</h6>
                    </div>
                    <div className='col-md-6'>
                      <h6 style={{
                        display: 'flex',
                        flexDirection: 'row-reverse'
                      }}
                      >
                        { `${catchRate}%` }
                      </h6>
                    </div>
                  </div>
                  <div className='row flex-container'>
                    <div className='col-md-6'>
                      <h6 style={{display: 'flex'}}>Gender Ratio:</h6>
                    </div>
                    <div className='col-md-6'>
						{ pokeData[indexNum-1].genderratio }
                    </div>
                  </div>
                </div>
                <div className='col-md-6'>
                  <div className='row flex-container'>
                    <div className='col-md-6'>
                      <h6 style={{display: 'flex'}}>Growth Rate:</h6>
                    </div>
                    <div className='col-md-6'>
                      <h6 style={{
                        display: 'flex',
                        flexDirection: 'row-reverse'
                      }}
                      >
                        {growthRate
                          .toLowerCase()
                          .split('-')
                          .map(letter => letter.charAt(0).toUpperCase() + letter.substring(1))
                          .join(' ')
                        }
                      </h6>
                    </div>
                  </div>
                  <div className='row flex-container'>
                    <div className='col-md-6'>
                      <h6 style={{display: 'flex'}}>Hatch Steps:</h6>
                    </div>
                    <div className='col-md-6'>
                      <h6 style={{
                        display: 'flex',
                        flexDirection: 'row-reverse'
                      }}
                      >
                        {hatchSteps}
                      </h6>
                    </div>
                  </div>
                  <div className='row flex-container'>
                    <div className='col-md-6'>
                      <h6 style={{display: 'flex'}}>Effort Values:</h6>
                    </div>
                    <div className='col-md-6'>
                      <h6 style={{
                        display: 'flex',
                        flexDirection: 'row-reverse'
                      }}
                      >
                        {pokeData[indexNum-1].evs }
                        
                      </h6>
                    </div>
                  </div>
                  <div className='row flex-container'>
                    <div className='col-md-6'>
                      <h6 style={{display: 'flex'}}>Abilities:</h6>
                    </div>
                    <div className='col-md-6'>
                      <h6 style={{
                        display: 'flex',
                        flexDirection: 'row-reverse'
                      }}
                      >
						{pokeData[indexNum-1].ability1}, {pokeData[indexNum-1].ability2}
                      </h6>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    )
  }

  return (
    <> 
      {pokemon === undefined && 
        <img 
          src={gif}
          alt='Loading'
        />
      }
      {pokemon !== undefined && pokemon && loadPokemonJsx(pokemon)}
      {pokemon === false && <h1> Pokémon Not Found</h1>} 
    </>
  )
}

export default Pokemon